.posts-main-page {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  cursor: pointer;
}
.posts-main-page .post-container {
  margin: 20px 0px;
  width: 90%;
  max-width: 650px;
  padding: 15px;
  min-height: 70px;
  background-color: #e1e8ec;
  border-radius: 15px;
  box-shadow: 0px 0px 7px 1px rgb(117, 115, 115);
}
.posts-main-page .post-container header {
  text-transform: capitalize;
  padding: 15px 0 15px;
  font-size: 21px;
  font-weight: bold;
}
.posts-main-page .post-container main {
  font-size: 20px;
}
.posts-main-page .post-container footer {
  width: auto;
  height: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.posts-main-page .post-container footer .like-button {
  min-width: 60px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 20px;
}
.posts-main-page .post-container footer button {
  background-color: transparent;
  font-weight: 500;
  border: none;
  font-size: 18px;
  height: 25px;
  border-bottom: 2px solid black;
}
.add-button-place {
  margin-top: 15px;
  width: 100px;
  max-width: 685px;
  height: 150px;
  border-radius: 20px;
  display: flex;
  align-items: flex-start;
  background-color: transparent;
  position: absolute;
  bottom: 0;
  right: 30px;
}
.add-button-place button {
  width: 100%;
  height: 70px;
  border: none;
  outline: none;
  text-transform: capitalize;
  font-size: 20px;
  border-radius: 20px;
  font-weight: bolder;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e1e8ec;
  box-shadow: 0px 0px 7px 1px rgb(117, 115, 115);
}
.add-post {
  position: absolute;
  top: 20%;
  width: 97%;
  max-width:500px ;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  background-color: white;
}
.add-post .text-input-parrent {
  width: 100%;
  display: flex;
  justify-content: center;
}
.add-post .text-input-parrent .text-input {
  width: 90%;
  height: 160px;
  overflow-y: auto;
  outline: none;
  border: 1px solid black;
  text-align: left;
  font-size: 18px;
}
.add-post-place {
  margin-top: -100px;
  position: absolute;
  width: 100%;
  height: calc(100vh + 100px);
  background-color: rgba(0, 0, 0, 0.651);
}
.add-post-button {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.add-post-button button {
  width: 90px;
  height: 30px;
  background-color: transparent;
  outline: none;
  border: none;
  font-size: 18px;
  font-weight: bold;
  border-radius: 13px;
  border-bottom: 2px solid rgb(72, 83, 149);
}
.search-place {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-place .search-inputs-parrent {
  width: 90%;
  max-width: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-place .search-inputs-parrent .search-icon {
  cursor: pointer;
  padding: 2px;
  z-index: 1;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  font-size: 20px;
  background-color: #172979;
  color: white;
  font-weight: bolder;
}
.search-place .search-inputs-parrent .hidden-input {
  width: 0px;
  height: 30px;
  border-radius: 15px;
  margin-left: -20px;
  outline: none;
  border: none;
  font-size: 18px;
  animation-name: hide-input;
  animation-duration: 2s;
  background-color: rgb(197, 204, 211);
}
.search-place .search-inputs-parrent .show-input {
  width: 250px;
  height: 35px;
  border-radius: 15px;
  outline: none;
  border: none;
  font-size: 18px;
  animation-name: show-input;
  animation-duration: 2s;
  background-color: rgb(197, 204, 211);

}
@keyframes hide-input {
  from {
    width: 250px;
    height: 35px;
    margin-left: 0;
  }
  to {
    width: 0px;
    display: none;
    margin-left: -20px;
  }
}
@keyframes show-input {
  from {
    width: 0px;
    height: 35px;
    margin-left: -20px;
  }
  to {
    width: 250px;
    margin-left: 0px;
  }
}
