.navigator-bar {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
}
.navigator-bar .navs-child {
  width: 90%;
  max-width: 900px;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navigator-bar .navs-child div:first-child {
  font-size: 25px;
  font-weight: bolder;
  word-spacing: -5px;
}
.navigator-bar .navs-child div:last-child {
  font-size: 23px;
}
.log-out {
  width: 80px;
  font-size: 17px;
  height: 30px;
  border: none;
  background-color: transparent;
  border-bottom: 2px solid rgb(187, 182, 175);
}
a{
  text-decoration: none;
  user-select: none;
}
