.comments-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;

}
.comments {
  width: 90%;
  max-width: 650px;
}
.comment-container {
  border-radius: 20px;
  width: 100%;
  margin-top: 20px;
  padding: 10px;
  box-shadow: 0px 0px 7px 1px rgb(117, 115, 115);
}
.comment-container header {
  height: 30px;
  font-size: 20px;
  text-transform: capitalize;
}
.comment-container main {
  height: auto;
  font-size: 20px;
}
.comment-container main::first-letter {
  text-transform: capitalize;
}
.comment-container footer {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.comment-container footer button {
  background-color: transparent;
  font-weight: 500;
  border: none;
  font-size: 18px;
  height: 25px;
  border-bottom: 2px solid black;
}
.comment-container footer .like-button {
  min-width: 60px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 20px;
}

.comments-page .write-comment {
  background-color: aliceblue;
  position: absolute;
  bottom: 0;
  width: 90%;
  max-width: 500px;
  height: 60px;
  display: flex;
  justify-content: center;
}
