.login-tags {
  height: 600px;
  border-radius: 50px;
  box-shadow: 0px 0px 15px 5px black;
  background-color: #e6e6e6;
  display: flex;
  align-items: center;
}
.login-elems {
  width: 400px;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.login-elems .input-label {
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.login-elems .input-label div {
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.login-elems .input-label label {
  font-size: 22px;
  text-align: center;
}
.login-elems .input-label input {
  width: 270px;
  border-radius: 4px 10px;
  text-transform: capitalize;
  outline: none;
  border: none;
  height: 27px;
  font-size: 18px;
  background-color: transparent;
  border-bottom: 3px solid #577474;
  border-left: 1px solid #577474;
}
.login-elems .input-label input[type="email"] {
  text-transform: none;
}
.login-elems .login-button {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.login-elems .login-button div {
  width: 108px;
  height: 42px;
  border-radius: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-elems .login-button button {
  width: 100px;
  height: 35px;
  border: none;
  font-size: 18px;
  font-weight: bold;
  font-family: "Courier New", Courier, monospace;
  border-radius: 15px;
  animation-duration: 4s;
  animation-name: border;
}
.share-place {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: #00000080;
}
.share-icons {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  background-color: rgb(184, 179, 179);
  z-index: 3;
  animation-name: share;
  animation-duration: 2s;
}
@keyframes share {
  from {
    height: 0px;
  }
  to {
    height: 300px;
  }
}
.share-icons .icons-place {
  width: 100%;
  max-width: 300px;
  height: 100px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.share-icons .icons-place button {
  width: 100px;
  height: 60px;
  font-size: 20px;
  border: none;
  outline: none;
  border-radius: 10px;
  background-color: rgb(157, 168, 177);
}
